.is-hidden {
  display: none;
}
#wifi-loader {
  --color: rgba(255, 60, 0, 0.513);

  width: 64px;
  height: 64px;
  border-radius: 50px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  svg {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    circle {
      position: absolute;
      fill: none;
      stroke-width: 6px;
      stroke-linecap: round;
      stroke-linejoin: round;
      transform: rotate(-100deg);
      transform-origin: center;

      &.back {
        stroke: var(--color);
      }

      &.front {
        stroke: var(--active_color);
      }
    }

    &.circle-outer {
      height: 86px;
      width: 86px;

      circle {
        stroke-dasharray: 62.75 188.25;

        &.back {
          animation: circle-outer 1.8s ease infinite 0.3s;
        }

        &.front {
          animation: circle-outer 1.8s ease infinite 0.15s;
        }
      }
    }

    &.circle-middle {
      height: 60px;
      width: 60px;

      circle {
        stroke-dasharray: 42.5 127.5;

        &.back {
          animation: circle-middle 1.8s ease infinite 0.25s;
        }

        &.front {
          animation: circle-middle 1.8s ease infinite 0.1s;
        }
      }
    }

    &.circle-inner {
      height: 34px;
      width: 34px;

      circle {
        stroke-dasharray: 22 66;

        &.back {
          animation: circle-inner 1.8s ease infinite 0.2s;
        }

        &.front {
          animation: circle-inner 1.8s ease infinite 0.05s;
        }
      }
    }
  }
}

@keyframes circle-outer {
  0% {
    stroke-dashoffset: 25;
  }

  25% {
    stroke-dashoffset: 0;
  }

  65% {
    stroke-dashoffset: 301;
  }

  80% {
    stroke-dashoffset: 276;
  }

  100% {
    stroke-dashoffset: 276;
  }
}

@keyframes circle-middle {
  0% {
    stroke-dashoffset: 17;
  }

  25% {
    stroke-dashoffset: 0;
  }

  65% {
    stroke-dashoffset: 204;
  }

  80% {
    stroke-dashoffset: 187;
  }

  100% {
    stroke-dashoffset: 187;
  }
}

@keyframes circle-inner {
  0% {
    stroke-dashoffset: 9;
  }

  25% {
    stroke-dashoffset: 0;
  }

  65% {
    stroke-dashoffset: 106;
  }

  80% {
    stroke-dashoffset: 97;
  }

  100% {
    stroke-dashoffset: 97;
  }
}

@keyframes text-animation {
  0% {
    clip-path: inset(0 100% 0 0);
  }

  50% {
    clip-path: inset(0);
  }

  100% {
    clip-path: inset(0 0 0 100%);
  }
}
