  --footer-bg-color: #f7f7f7;
  --footer-text-color: #545454;
  --backdrop-color: #00000040;
  --modal-text-color: #8c8c8c;
}

html {
  box-sizing: border-box;
}

h1, h2, h3, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  padding: unset;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

:after, :before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  font-family: Roboto, sans-serif;
}

button {
  cursor: pointer;
}

.container {
