#wifi-loader {
  --color: #ff3c0083;
  width: 64px;
  height: 64px;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#wifi-loader svg {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#wifi-loader svg circle {
  fill: none;
  stroke-width: 6px;
  stroke-linecap: round;
  stroke-linejoin: round;
  transform-origin: center;
  position: absolute;
  transform: rotate(-100deg);
}

#wifi-loader svg circle.back {
  stroke: var(--color);
}

#wifi-loader svg circle.front {
  stroke: var(--active_color);
}

#wifi-loader svg.circle-outer {
  height: 86px;
  width: 86px;
}

#wifi-loader svg.circle-outer circle {
  stroke-dasharray: 62.75 188.25;
}

#wifi-loader svg.circle-outer circle.back {
  animation: 1.8s .3s infinite circle-outer;
}

#wifi-loader svg.circle-outer circle.front {
  animation: 1.8s .15s infinite circle-outer;
}

#wifi-loader svg.circle-middle {
  height: 60px;
  width: 60px;
}

#wifi-loader svg.circle-middle circle {
  stroke-dasharray: 42.5 127.5;
}

#wifi-loader svg.circle-middle circle.back {
  animation: 1.8s .25s infinite circle-middle;
}

#wifi-loader svg.circle-middle circle.front {
  animation: 1.8s .1s infinite circle-middle;
}

#wifi-loader svg.circle-inner {
  height: 34px;
  width: 34px;
}

#wifi-loader svg.circle-inner circle {
  stroke-dasharray: 22 66;
}

#wifi-loader svg.circle-inner circle.back {
  animation: 1.8s .2s infinite circle-inner;
}

#wifi-loader svg.circle-inner circle.front {
  animation: 1.8s 50ms infinite circle-inner;
}

@keyframes circle-outer {
  0% {
    stroke-dashoffset: 25px;
  }

  25% {
    stroke-dashoffset: 0;
  }

  65% {
    stroke-dashoffset: 301px;
  }

  80% {
    stroke-dashoffset: 276px;
  }

  100% {
    stroke-dashoffset: 276px;
  }
}

@keyframes circle-middle {
  0% {
    stroke-dashoffset: 17px;
  }

  25% {
    stroke-dashoffset: 0;
  }

  65% {
    stroke-dashoffset: 204px;
  }

  80% {
    stroke-dashoffset: 187px;
  }

  100% {
    stroke-dashoffset: 187px;
  }
}

@keyframes circle-inner {
  0% {
    stroke-dashoffset: 9px;
  }

  25% {
    stroke-dashoffset: 0;
  }

  65% {
    stroke-dashoffset: 106px;
  }

  80% {
    stroke-dashoffset: 97px;
  }

  100% {
    stroke-dashoffset: 97px;
  }
}

@keyframes text-animation {
  0% {
    -webkit-clip-path: inset(0 100% 0 0);
    clip-path: inset(0 100% 0 0);
  }

  50% {
    -webkit-clip-path: inset(0);
    clip-path: inset(0);
  }

  100% {
    -webkit-clip-path: inset(0 0 0 100%);
    clip-path: inset(0 0 0 100%);
  }
}

